.header-profile {
    /* Auto layout */
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 20px;
    gap: 20px;
    
    background: #091824;
    border-radius: 0px 0px 12px 12px;
    color: #ffffff;
    
}

.header-profile svg {
    width: 24px;
    height: 24px;
    color: #ffffff;
}

.header-profile__user {
    /* Auto layout */
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    padding: 0px;
    gap: 7px;

    font-size: 16px;
}
